.navbar {
  background-color: rgb(9, 63, 39);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
}
.navbar .navbar-logo {
  height: 100%;
  margin-left: -15rem;
  margin-right: 5rem;
}
.navbar .navbar-categories {
  font-size: 1.2rem;
  gap: 1.5rem;
  display: flex;
}
.navbar .navbar-categories .navbar-link {
  color: rgb(225, 224, 224);
  text-decoration: none;
  padding: 1rem;
  border: 1px solid rgb(9, 63, 39);
  border-radius: 1rem;
}
.navbar .navbar-categories .navbar-link:hover {
  border: 1px solid rgb(255, 255, 255);
}

